import degreesToRadians from 'utils/trigonometry/degrees-to-radians';

import { ColorWheel, DEFAULT_REGION_SIZE } from './color-wheel';
import './color-wheel.css';

export default async function draw(videoElement) {
  const [hueRegionSize, saturationRegionSize] = DEFAULT_REGION_SIZE;

  const parsingResults = [];

  const yieldedResults = truesight.popularizeVideo({
    videoElement,
    quality: 8,
    secondsBetweenFrames: 10,
  });

  const canvasElement = document.createElement('canvas');
  canvasElement.width = videoElement.offsetHeight;
  canvasElement.height = videoElement.offsetHeight;

  videoElement.parentNode.appendChild(canvasElement);

  canvasElement.style.right = '20px';
  // canvasElement.style.bottom = '30px';

  const canvasContext = canvasElement.getContext('2d');

  canvasContext.translate(canvasElement.width / 2, canvasElement.height / 2);
  canvasContext.rotate(-Math.PI / 2 + degreesToRadians(-hueRegionSize / 2));

  const colorWheel = ColorWheel(DEFAULT_REGION_SIZE);

  const expectedNumberOfFrames = Math.floor(videoElement.duration / 10) + 1;

  for await (const yieldedResult of yieldedResults) {
    canvasContext.beginPath();
    const whiteTers = 'hsl(0, 0%, 96%)';
    canvasContext.fillStyle = whiteTers;
    canvasContext.strokeStyle = whiteTers;
    const endAngle = yieldedResult.index / expectedNumberOfFrames * 360;
    console.log(endAngle);
    canvasContext.arc(0, 0, 0.4 * canvasElement.width, 0, degreesToRadians(endAngle));
    canvasContext.lineTo(0, 0);
    canvasContext.closePath();
    canvasContext.fill();
    canvasContext.stroke();

    parsingResults.push(yieldedResult);
  }

  // canvasContext.beginPath();
  // const whiteTer = 'hsl(0, 0%, 96%)';
  // canvasContext.fillStyle = whiteTer;
  // canvasContext.strokeStyle = whiteTer;
  // canvasContext.arc(0, 0, 0.48 * canvasElement.width, 0, 2 * Math.PI);
  // canvasContext.closePath();
  // canvasContext.fill();
  // canvasContext.stroke();
}
