import degreesToRadians from 'utils/trigonometry/degrees-to-radians';

export const DEFAULT_REGION_SIZE = [45, 20];

export function ColorWheel(regionSize = DEFAULT_REGION_SIZE) {
  const colorWheel = [];

  const [hueRegionSize, saturationRegionSize] = regionSize;

  for (let i = 0; i < 360; i += hueRegionSize) {
    const saturations = [];

    const startAngle = degreesToRadians(i);
    const endAngle = degreesToRadians(i + hueRegionSize);

    for (let j = 0; j < 100; j += saturationRegionSize) {
      saturations.push({
        bounds: [j, j + saturationRegionSize],
        startAngle,
        endAngle,
        r: j + saturationRegionSize,
      });
    }

    colorWheel.push({
      bounds: [i, i + hueRegionSize],
      saturations,
    });
  }

  return colorWheel;
}

export function mapColorToRegion(color, colorWheel) {
  const hues = colorWheel.hues.find((hue) => {
    if (color[0] === 360) {
      return colorWheel[0];
    }

    return color[0] >= hue.bounds[0] && color[0] < hue.bounds[1];
  });

  return hues.saturations.find((saturation) => {
    if (color[1] === 100) {
      return hues.saturations[0];
    }

    return color[1] >= saturation.bounds[0] && color[1] < saturation.bounds[1];
  });
}
